
import { defineComponent, onMounted, onUnmounted, ref } from 'vue'
import { storeToRefs } from 'pinia'
import { useMainStore, useSlidesStore } from '@/store'
import useGlobalHotkey from '@/hooks/useGlobalHotkey'
import usePasteEvent from '@/hooks/usePasteEvent'
import useScreening from '@/hooks/useScreening'

import HotkeyDoc from './EditorHeader/HotkeyDoc.vue'
import EditorHeader from './EditorHeader/index.vue'
import Canvas from './Canvas/index.vue'
import CanvasTool from './CanvasTool/index.vue'
import Thumbnails from './Thumbnails/index.vue'
import Toolbar from './Toolbar/index.vue'
import Remark from './Remark/index.vue'
import ExportDialog from './ExportDialog/index.vue'
import { cloneDeep } from 'lodash'
import Screen from '../Screen/index.vue'


export default defineComponent({
  name: 'editor',
  components: {
    // EditorHeader,
    Canvas,
    CanvasTool,
    Thumbnails,
    Toolbar,
    Remark,
    ExportDialog,
    HotkeyDoc,
    Screen
  },
  props: {
    editable: {
      type: Boolean,
      default: true
    }
  },
  setup() {
    const slidesStore = useSlidesStore()
    const mainStore = useMainStore()
    const { dialogForExport } = storeToRefs(mainStore)
    const { enterScreening, enterScreeningFromStart, exitScreening } = useScreening()
    const setDialogForExport = mainStore.setDialogForExport
    const setSlides = slidesStore.setSlides


    const closeExportDialog = () => mainStore.setDialogForExport('')

    const remarkHeight = ref(40)
    const hotkeyDrawerVisible = ref(false)

    useGlobalHotkey()
    usePasteEvent()

    onMounted(() => {
      window.addEventListener('message', dealMessage, false)
    })

    onUnmounted(() => {
      window.removeEventListener('message', dealMessage)
    })

    // message监听
    const dealMessage = ( e: any ) => {
      const {type, action, data} = e.data
      if ( type === 'online-editor' ) {
        
        // 初始化数据
        if (action === 'init') setSlides(data)
        // 演示(外部调用有问题，内部调用没问题)
        else if ( action === 'play' ) {
          if (data === 'nowPage') enterScreening()
          else if (data === 'firstPage') enterScreeningFromStart()
        }
        else if (action === 'closeFullScreen') exitScreening()
        // 导出
        else if (action === 'export') setDialogForExport('pptx')
        // 快捷键 
        else if (action === 'shortcut') hotkeyDrawerVisible.value = true
        // 获取当前json
        else if (action === 'handleSave') {
          const tempData = cloneDeep(slidesStore.slides)
          window.parent?.postMessage({type: 'ppt', action: 'handleSave', data: tempData }, '*')
        }

      }
    }



    return {
      remarkHeight,
      dialogForExport,
      hotkeyDrawerVisible,
      closeExportDialog,
      enterScreening,
      enterScreeningFromStart
    }
  },
})
