
import { defineComponent, onMounted, onUnmounted, ref } from 'vue'
import { KEYS } from '@/configs/hotkey'
import useScreening from '@/hooks/useScreening'

import BaseView from './BaseView.vue'
import PresenterView from './PresenterView.vue'

export default defineComponent({
  name: 'pptist-screen',
  components: {
    BaseView,
    PresenterView,
  },
  setup() {
    const viewMode = ref<'base' | 'presenter'>('base')

    const changeViewMode = (mode: 'base' | 'presenter') => {
      viewMode.value = mode
    }

    const { exitScreening } = useScreening()

    // 快捷键退出放映
    const keydownListener = (e: KeyboardEvent) => {
      const key = e.key.toUpperCase()
      if (key === KEYS.ESC) exitScreening()
    }

    onMounted(() => {
      document.addEventListener('keydown', keydownListener)
      window.addEventListener('message', dealMessage)
    })

    onUnmounted(() => {
      document.removeEventListener('keydown', keydownListener)
      window.removeEventListener('message', dealMessage)
    })

    // 监听退出全屏
    const dealMessage = ( e: any ) => {
      const {type, action, data} = e.data
      if ( type === 'online-editor' ) {
        if (action === 'closeFullScreen') exitScreening()
      }
    }

    
    return {
      viewMode,
      changeViewMode,
    }
  },
})
